import Carousel from 'react-bootstrap/Carousel';
import "./CSS/HomeCarousel.css"

function HomeCarousel(){
  return (
    // .
    <Carousel className='viewcard'> 
      <Carousel.Item>
        <img
          className="viewcard"
          src={process.env.PUBLIC_URL + "images/Carousel1.jpg"} alt="Carousel1"
        />
        <Carousel.Caption>
          <h3 className='hfont'>   </h3>
          <p className='hfont'>   </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="viewcard"
          src={process.env.PUBLIC_URL + "images/Carousel2.jpg"} alt="Carousel2"
        />

        <Carousel.Caption>
          <h3 className='hfont'>Planet   </h3>
          <p className='hfont'>    </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="viewcard"
          src={process.env.PUBLIC_URL + "images/Carousel3.jpg"} alt="Carousel3"
        />

        <Carousel.Caption>
          <h3 className='hfont'>  </h3>
          <p className='hfont'>
            Planet   .
          </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="viewcard"
          src={process.env.PUBLIC_URL + "images/Carousel4.jpg"} alt="Carousel4"
        />

        <Carousel.Caption>
          <h3 className='hfont'>    </h3>
          <p className='hfont'>
          Planet    
          </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="viewcard"
          src={process.env.PUBLIC_URL + "images/Carousel5.jpg"} alt="Carousel5"
        />

        <Carousel.Caption>
          <h3 className='hfont2'>  Up!  Down! </h3>
          <p className='hfont2'>
                 Planet 
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}

export default HomeCarousel;

